@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:700|Roboto+Mono:700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.josefina{
	font-family: 'Josefin Sans', sans-serif;
}

.robo-mono{
	font-family: 'Roboto Mono', monospace;
	font-weight: 700;
	font-size: 80px;
	margin: unset;
}

/* common classes */
.flex-col{
	display: flex;
	flex-direction: column;
}

.flex-row{
	display: flex;
	flex-direction: row;
}

.flex-col-centered{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}


/* app styles */

.App {
	text-align: center;
	width: 100vw;
	height: 100vh;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.image{
	flex-basis: 30%;
}

.image img{
	/* width: 10vw; */
	height: 100vh;
}

.content{
	flex-basis: 70%;
}

.header{
	justify-content:space-between;
	height: 7vh;
	padding: 3px;
}

.signature{
	height: 30px;
	margin-top: 5px;
	margin-right: 5px;
}

.brand{
	width: 60px;
	height: 40px;
}

.body{
	height: 92vh;
}

#first{
	font-weight: 700;
	font-size: 20px;
	margin: unset;
}
